const assertShow = `// 类型断言 “ 值 as 类型 ” 或 “ <类型>值 ”

// 1、将一个联合类型断言为其中一个类型
// 2、将一个父类断言为更具体的子类
// 3、将任何一个类型断言为 any
// 4、将 any 断言为一个具体的类型
// 5、要使得 A 能够被断言为 B，只需要 A 兼容 B 或 B 兼容 A 即可

// 双重断言：numberVar as any as stringVar，除非迫不得已，千万别用双重断言。

// 类型断言只能够「欺骗」TypeScript 编译器，无法避免运行时的错误，示例1传入的值为 Cat 运行时会报错。
// 使用类型断言时一定要格外小心，尽量避免断言后调用方法或引用深层属性，以减少不必要的运行时错误。

// 示例1：联合类型 as 其中一个类型
interface Cat {
  name: string;
  run(): void;
}
interface Fish {
  name: string;
  swim(): void;
}
function isFish(animal: Cat | Fish) {
  
  if (typeof (animal as Fish).swim === 'function') {
      return true;
  }
  return false;
}

// 示例2：任何一个类型 as any
(window as any).name = 'window';
`;

