const enumShow = `// 枚举
// 用于取值被限定在一定范围内的场景，如：一周七天、12个月、固顶颜色等；
// 枚举项有两种类型：常数项（constant member）和计算所得项（computed member）

// 常数项
enum Color1 {Red, Green, Blue}
console.log(Color1["Red"] === 0); // true
console.log(Color1[1] === "Green"); // true

// 计算所得项
enum Color2 {Red, Green, Blue = "color-blue".length}
console.log(Color2["Blue"]) // 10

// 手动赋值
enum Mounths {January, February, March = 3,  April}
console.log(Mounths["January"]); // 0
console.log(Mounths["February"]); // 1
console.log(Mounths["March"]); // 3
console.log(Mounths["April"]); // 4

// 常数枚举（const enum）：编译阶段被删除，并且不能包含计算成员
const enum Days1 {Sun, Mon, Tue}
const daysList1 = [Days1.Sun, Days1.Mon, Days1.Tue];
console.log(daysList1); // [0, 1, 2]
console.log(Days1); ❌ // {0: "Sun", 1: "Mon", 2: "Tue", Sun: 0, Mon: 1, Tue: 2}

// 外部枚举（declare enum）：编译结果中被删除
declare enum Days2 {Sun, Mon, Tue}
const daysList2 = [Days2.Sun, Days2.Mon, Days2.Tue]; ❌ // Days2 is not defined

// declare 和 const 同时使用
declare const enum Days3 {Sun, Mon, Tue}
const daysList3 = [Days3.Sun, Days3.Mon, Days3.Tue]; ❌ // Days3 is not defined
`;

const classDefineShow = `// 类的概念
类(Class)：定义了一件事物的抽象特点，包含它的属性和方法。
对象（Object）：类的实例，通过 new 生成。
面向对象（OOP）的三大特性：封装、继承、多态。
封装（Encapsulation）：将对数据的操作细节隐藏起来，只暴露对外的接口。外界通过对外提供的接口来访问该对象，同时也保证了外界无法任意更改对象内部的数据。
继承（Inheritance）：子类继承父类，子类除了拥有父类的所有特性外，还有一些更具体的特性。
多态（Polymorphism）：由继承而产生了相关的不同的类，对同一个方法可以有不同的响应。
存取器（getter & setter）：用以改变属性的读取和赋值行为。
修饰符（Modifiers）：修饰符是一些关键字，用于限定成员或类型的性质。如 public。
抽象类（Abstract Class）：抽象类是供其他类继承的基类，抽象类不允许被实例化。抽象类中的抽象方法必须在子类中被实现。
接口（Interfaces）：不同类之间公有的属性或方法，可以抽象成一个接口。接口可以被类实现。一个类只能继承自另一个类，但是可以实现多个接口
`;

const classShow1 = `// 类 Class
// ES6: constructor、属性、方法、类的继承（extends）、存取器（getter & setter）、静态方法（static）
// ES7: 实例属性、静态属性

// TypeScript: public、private、protected
// TypeScript 编译后的代码，并没有限制 private 属性在外部的可访问性;
// private 修饰的属性或方法，子类中是不允许访问的;
// protected 修饰的属性或方法，子类中是允许访问的;
// constructor 构造函数修饰为 private 时，该类不允许被继承或者实例化；
// constructor 构造函数修饰为 protected 时，该类只允许被继承；

class Student {
  public name: string; // 公有的
  private age: number; // 私有的
  protected group: string; // 受保护的
  public constructor(name: string, age: number, group: string) {
      this.name = name;
      this.age = age;
      this.group = group;
  }
}

let a = new Student('张三', 10, '三年级一班');
a.age = 12; ❌
a.group = '四年级一班'; ❌
console.log(a.age); ❌// 12
console.log(a.group); ❌// 四年级一班
console.log(a.name); // 张三
`;

const classShow2 = `// 类 Class
// 参数属性、只读属性（readonly）
class Teacher {
  readonly group: string; // 只读属性
  private readonly age: number; // 私有的只读属性
  // 参数属性 'public name' 
  public constructor(public name: string, age: number, group: string) {
      this.name = name;
      this.age = age;
      this.group = group;
  }
}

let t = new Teacher('李老师', 30, '三年级一班');
t.age = 32; ❌
t.group = '四年级一班'; ❌
console.log(t.age); ❌// 32
console.log(t.group); // 四年级一班
console.log(t.name); // 李老师
`;

const classShow3 = `// 类 Class
// 抽象类，abstract 用于定义抽象类和其中的抽象方法
// 抽象类是不允许被实例化的
// 抽象类中的抽象方法必须被子类实现

abstract class Animal { // abstract 定义抽象类
  public name: string;
  public constructor(name: string) {
      this.name = name;
  }
  public abstract sayHi(): any; // abstract 定义抽象类中的抽象方法
}

class Cat extends Animal { // 抽象类不能被示例，但能被继承
  public sayHi() { // 抽象类中的抽象方法必须被子类实现
    console.log('Meow, My name is ' + this.name);
  }
}

let cat = new Cat('Tom');
cat.sayHi(); // Meow, My name is Tom
`;

