/* eslint-disable import/no-dynamic-require, global-require */

/**
 * 接口请求
 */
import axios from 'axios';

const TIMEOUT_TIME = 10000;
const TIMEOUT_MESSAGE = {
  code: -10,
  message: '请求超时了',
};

/**
 * axios 封装 jsonp
 * @param {*} options
 */
axios.jsonp = (options = {}) => {
  if (!options.url) {
    return Promise.reject(new Error());
  }

  let srcParams = '';
  if (options.params) {
    const { params } = options;
    Object.keys(params).forEach((item) => {
      srcParams = `&${item}=${encodeURIComponent(params[item])}`;
    });
    srcParams += `&time=${Date.now()}`;
  }

  const headEle = document.getElementsByTagName('head')[0];
  const callback = `CALLBACK${Date.now()}`;
  const JSONP = document.createElement('script');
  JSONP.setAttribute('type', 'text/javascript');
  JSONP.src = `${options.url}?callback=${callback}${srcParams}`;

  return new Promise((resolve, reject) => {
    let timer = setTimeout(() => {
      reject(TIMEOUT_MESSAGE);
    }, TIMEOUT_TIME);
    window[callback] = (res) => {
      clearTimeout(timer);
      timer = null;
      resolve(res);
      HTMLHeadingElement.removeChild(JSONP);
      delete window[callback];
    };
    headEle.appendChild(JSONP);
  });
};

/**
 * get、post 请求
 * @param {*} options
 */
const requestHandler = (() => (options = {}) => {
  console.log('request-options', options);
  if (!options.url) {
    return Promise.reject(new Error());
  }
  return new Promise((resolve, reject) => {
    let timer = setTimeout(() => {
      reject(TIMEOUT_MESSAGE);
    }, TIMEOUT_TIME);
    axios(options)
      .then((res) => {
        clearTimeout(timer);
        timer = null;
        resolve(res);
      })
      .catch((err) => {
        clearTimeout(timer);
        timer = null;
        reject(err);
      });
  });
})();

/**
 * jsonp 请求
 * @param {*} options
 */
const jsonpRequestHandler = (() => (options = {}) => {
  console.log('request-options', options);
  if (!options.url) {
    return Promise.reject(new Error());
  }
  return new Promise((resolve, reject) => {
    let timer = setTimeout(() => {
      reject(TIMEOUT_MESSAGE);
    }, TIMEOUT_TIME);
    axios.jsonp(options)
      .then((res) => {
        clearTimeout(timer);
        timer = null;
        resolve(res);
      })
      .catch((err) => {
        clearTimeout(timer);
        timer = null;
        reject(err);
      });
  });
})();

/**
 * jsonp 请求
 * @param {*} options
 */
const mocksRequestHandler = (() => (filename = '') => {
  console.log('request-filename', filename);
  if (!filename) {
    return Promise.reject(new Error('Mock文件不存在'));
  }
  return Promise.resolve(require(`../mock/${filename}.js`).default || {});
})();

export {
  requestHandler,
  jsonpRequestHandler,
  mocksRequestHandler,
};
