<template>
  <div class="box">
    <div class="btns-wrap">
      <div class="btns">
        <div
          v-for="(item, i) in chart1List"
          :key="i"
          @click="createChart1(1, i)"
        >图例1: {{item.name}}</div>
      </div>
      <div class="btns">
        <div
          v-for="(item, i) in chart2List"
          :key="i"
          @click="createChart2(2, i)"
        >图例2: {{item.name}}</div>
      </div>
    </div>
    <div
      v-for="(item, i) in 2"
      :key="i"
      :id="'chart'+(i+1)"
      class="chart"
    ></div>
  </div>
</template>
<script>
import G6 from '@antv/g6';

export default {
  data() {
    return {
      chart1List: [
        { name: '网格布局布局' },
        { name: '辐射布局' },
        { name: '环形布局' },
        { name: '同心圆' },
        { name: 'mds 布局' },
        { name: 'fruchterman 布局' },
      ],
      chart2List: [
        { name: '紧凑树' },
        { name: '生态树' },
        { name: '缩进树' },
        { name: '辐射树' },
        { name: '脑图' },
      ],
      data1: {
        nodes: [
          { id: '0', label: '0', des: '这是第 0 个节点' },
          { id: '1', label: '1', des: '这是第 1 个节点' },
          { id: '2', label: '2', des: '这是第 2 个节点' },
          { id: '3', label: '3', des: '这是第 3 个节点' },
          { id: '4', label: '4', des: '这是第 4 个节点' },
          { id: '5', label: '5', des: '这是第 5 个节点' },
          { id: '6', label: '6', des: '这是第 6 个节点' },
          { id: '7', label: '7', des: '这是第 7 个节点' },
          { id: '8', label: '8', des: '这是第 8 个节点' },
          { id: '9', label: '9', des: '这是第 9 个节点' },
        ],
        edges: [
          { source: '0', target: '1' },
          { source: '0', target: '2' },
          { source: '0', target: '4' },
          { source: '0', target: '8' },
          { source: '0', target: '9' },
          { source: '1', target: '2' },
          { source: '1', target: '4' },
          { source: '1', target: '6' },
          { source: '1', target: '8' },
          { source: '2', target: '3' },
          { source: '2', target: '5' },
          { source: '2', target: '7' },
          { source: '3', target: '4' },
          { source: '3', target: '6' },
          { source: '3', target: '8' },
          { source: '4', target: '5' },
          { source: '4', target: '7' },
          { source: '4', target: '9' },
          { source: '5', target: '6' },
          { source: '5', target: '8' },
          { source: '6', target: '7' },
          { source: '6', target: '8' },
          { source: '6', target: '9' },
          { source: '7', target: '9' },
        ],
      },
      data2: {
      },
      graph1: null,
      graph2: null,
    };
  },
  async created() {
    this.data2 = await fetch(
      'https://gw.alipayobjects.com/os/antvdemo/assets/data/algorithm-category.json',
    ).then((res) => res.json());
    this.createChart1(1, 0);
    this.createChart2(2, 0);
  },
  methods: {
    /**
     * 一般图
     */
    createChart1(index, layoutType) {
      if (this.graph1) {
        this.graph1.destroy();
      }

      const layout = [
        {
          // 网格布局
          type: 'grid',
          begin: [10, 10],
          width: 500 - 10,
          height: 350 - 10,
        },
        {
          // 辐射布局
          type: 'radial',
          unitRadius: 50,
        },
        {
          // 环形布局
          type: 'circular',
          startRadius: 10,
          endRadius: 300,
        },

        {
          // 同心圆
          type: 'concentric',
          maxLevelDiff: 0.5,
        },
        {
          // mdc
          type: 'mds',
          linkDistance: 100,
        },
        {
          // fruchterman 原理上而言属于力导向布局算法
          type: 'fruchterman',
          gravity: 5,
          speed: 5,
        },
      ];

      this.graph1 = new G6.Graph({
        container: `chart${index}`,
        width: 500,
        height: 350,
        fitView: true, // 自适应画布大小
        fitViewPadding: 30, // 自适应四周留白
        layout: layoutType ? layout[layoutType] : layout[0],
        modes: {
          default: [
            'zoom-canvas',
            'drag-canvas',
            'drag-node',
            {
              type: 'tooltip',
              formatText: (model) => model.des,
              offset: 0,
            },
          ],
        },
        defaultNode: {
          type: 'circle',
          size: 20,
          style: {
            fill: '#a0d911',
          },
          labelCfg: {
            style: {
              fill: '#fff',
              fontSize: 12,
            },
          },
        },
        defaultEdge: {
          type: 'line-with-arrow',
          size: 1,
          color: '#e2e2e2',
          style: {
            fill: '#c41d7f',
            fontSize: 12,
          },
          labelCfg: {
            style: {
              fill: '#fff',
              fontSize: 12,
            },
          },
        },
      });

      this.graph1.data(this.data1);

      this.graph1.render();
    },

    /**
     * 树图、脑图
     */
    createChart2(index, layoutType) {
      if (this.graph2) {
        this.graph2.destroy();
      }

      const layout = [
        {
          // 紧凑树
          type: 'compactBox',
          direction: 'LR',
        },
        {
          // 生态树
          type: 'dendrogram',
          direction: 'LR',
        },
        {
          // 缩进树
          type: 'indented',
          direction: 'LR',
          isHorizontal: true,
          indent: 30,
          getHeight: () => 18,
          getWidth: () => 18,
          getVGap: () => 10,
          getHGap: () => 60,
        },
        {
          // 辐射树
          type: 'dendrogram',
          direction: 'LR',
          nodeSep: 20,
          rankSep: 100,
          radial: true,
        },
        {
          // 脑图
          type: 'mindmap',
          direction: 'H',
          getHeight: () => 18,
          getWidth: () => 18,
          getVGap: () => 10,
          getHGap: () => 60,
          // 脑图时覆盖 direction: 'H' 属性
          getSide: () => 'left',
        },
      ];

      this.graph2 = new G6.TreeGraph({
        container: `chart${index}`,
        width: 500,
        height: 350,
        fitView: true, // 自适应画布大小
        fitViewPadding: 30, // 自适应四周留白
        modes: {
          default: [
            'zoom-canvas',
            'drag-canvas',
            'drag-node',
            { // 展开折叠功能
              type: 'collapse-expand',
              onChange: (item, collapsed) => {
                const { data } = item.get('model');
                data.collapsed = collapsed;
                return true;
              },
            },
          ],
        },
        layout: layoutType ? layout[layoutType] : layout[0],
        defaultNode: { // 点集样式
          type: 'circle',
          size: 30,
          anchorPoints: [
            [0, 0.5],
            [1, 0.5],
          ],
          style: {
            fill: '#a0d911',
            stroke: '#5B8FF9',
          },
        },
        defaultEdge: { // 边集样式
          type: 'cubic-horizontal',
          style: {
            stroke: '#A3B1BF',
          },
        },
      });

      this.graph2.data(this.data2);

      this.graph2.render();
    },
  },
};
</script>
