  min-height: calc(94%);
  padding: 20px;
  margin: 20px;
  background: #fff;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.btns-wrap {
  margin-bottom: 30px;
}
.btns {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  div {
    cursor: pointer;
    margin: 0 10px;
    width: 180px;
    height: 36px;
    line-height: 36px;
    border-radius: 5px;
    text-align: center;
    background: coral;
    color: #fff;
  }
}
.chart {
  background: ghostwhite;
  width: 500px;
  height: 350px;
  border-radius: 3px;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
