

























































/* eslint-disable */
import { Component, Vue, Provide } from 'vue-property-decorator';
import G2chart from './components/G2chart.vue';
import G2Plot from './components/G2Plot.vue';
import G6chart from './components/G6chart.vue';
import TypeScript from './components/TypeScript.vue';
import ThingsList from './components/ThingsList.vue';
import ThingsFamily from './components/ThingsFamily.vue';
import ThingsLove from './components/ThingsLove.vue';

@Component({
  components: {
    G2chart,
    G2Plot,
    G6chart,
    TypeScript,
    ThingsList,
    ThingsFamily,
    ThingsLove,
  }
})
export default class App extends Vue {
  // 数据
  @Provide() collapsed = true;
  @Provide() realIndex = 1;
  @Provide() headerTitle = '100 件事';
  @Provide() headerSubTitle = '一个人可以做的100件事';
  @Provide() titleArr: object[] = [
    {
      key: 1,
      icon: 'unordered-list',
      title: '个人 100 件事',
      subtitle: '一个人可以做的100件事'
    },
    {
      key: 2,
      icon: 'unordered-list',
      title: '家庭 100 件事',
      subtitle: '孩子一起做的100件事'
    },
    {
      key: 3,
      icon: 'unordered-list',
      title: '情侣 100 件事',
      subtitle: '情侣间可以做的100件事'
    },
    {
      key: 4,
      icon: 'line-chart',
      title: 'G2 图表示例',
      subtitle: '基于G2的可视化图表示例'
    },
    {
      key: 5,
      icon: 'bar-chart',
      title: 'G2Plot 图表示例',
      subtitle: '基于G2Plot的可视化图表示例'
    },
    {
      key: 6,
      icon: 'dot-chart',
      title: 'G6 图表示例',
      subtitle: '基于G6的可视化图表示例'
    },
    {
      key: 7,
      icon: 'code',
      title: 'TypeScript 示例',
      subtitle: 'https://ts.xcatliu.com/'
    },
  ];

  // 方法
  private handleClick (result: any) {
    this.realIndex = +result.key || 1;
    const titleObj: any = this.titleArr[this.realIndex - 1];
    this.headerTitle = titleObj.title;
    this.headerSubTitle = titleObj.subtitle;
  }
}
