<script>
// 瀑布流: https://github.com/desandro/masonry
import Masonry from 'masonry-layout';
// 图片预加载: https://imagesloaded.desandro.com/
import imagesLoaded from 'imagesloaded';
// 代码美化: https://github.com/highlightjs
// dark theme: ['atom-one-dark', 'monokai', 'tomorrow-night-bright', 'vs2015']
// light theme: ['arduino-light', 'tomorrow', 'xcode', 'atelier-seaside-light']
// import hljs from 'highlight.js'; // import all
import hljs from 'highlight.js/lib/core';
import javascript from 'highlight.js/lib/languages/javascript';
import typescript from 'highlight.js/lib/languages/typescript';
import typescriptData from '../script/ts/typescript';
import 'highlight.js/styles/arduino-light.css';

hljs.registerLanguage('javascript', javascript);
hljs.registerLanguage('typescript', typescript);

export default {
  data() {
    return {
      tsData: typescriptData,
    };
  },
  mounted() {
    // 代码高亮初始化
    document.querySelectorAll('pre code').forEach((block) => {
      hljs.highlightBlock(block);
    });
