<template>
  <div class="box">
    <div class="btns">
      <div
        v-for="(item, i) in 4"
        :key="i"
        @click="download(i, '图例'+(i+1)+'下载')"
      >图例{{i+1}}下载</div>
    </div>
    <div
      v-for="(item, i) in 4"
      :key="i"
      :id="'chart'+(i+1)"
      class="chart"
    ></div>
  </div>
</template>
<script>
import { Chart } from '@antv/g2';
import chartDownload from '../script/js/G2Download';

export default {
  mounted() {
    this.chart1 = this.createChart1(1);
    this.chart2 = this.createChart2(2);
    this.chart3 = this.createChart3(3);
    this.chart4 = this.createChart4(4);
  },
  methods: {
    /**
     * 下载图例
     */
    download(index, name) {
      const chartArr = [this.chart1, this.chart2, this.chart3, this.chart4];
      chartDownload.downloadImage(chartArr[index], name);
    },

    /**
     * 链式操作渲染示例
     *
     * Step 1: 创建 Chart 对象, new Chart({})
     * Step 2: 载入原始数据
     * Step 3: 创建图形语法
     * Step 4: 渲染图表
     */
    createChart1(index) {
      const data = [
        { feature: 'Battery', value: 0.22, phone: 'iPhone' },
        { feature: 'Brand', value: 0.28, phone: 'iPhone' },
        { feature: 'Contract', value: 0.29, phone: 'iPhone' },
        { feature: 'Design', value: 0.17, phone: 'iPhone' },
        { feature: 'Internet', value: 0.22, phone: 'iPhone' },
        { feature: 'Battery', value: 0.27, phone: 'Samsung' },
        { feature: 'Brand', value: 0.16, phone: 'Samsung' },
        { feature: 'Contract', value: 0.35, phone: 'Samsung' },
        { feature: 'Design', value: 0.13, phone: 'Samsung' },
        { feature: 'Internet', value: 0.2, phone: 'Samsung' },
        { feature: 'Battery', value: 0.26, phone: 'Nokia Smartphone' },
        { feature: 'Brand', value: 0.1, phone: 'Nokia Smartphone' },
        { feature: 'Contract', value: 0.3, phone: 'Nokia Smartphone' },
        { feature: 'Design', value: 0.14, phone: 'Nokia Smartphone' },
        { feature: 'Internet', value: 0.22, phone: 'Nokia Smartphone' },
      ];

      const chart = new Chart({
        // renderer: 'svg',
        renderer: 'canvas',
        container: `chart${index}`,
        width: 450,
        height: 300,
      });

      chart.data(data);

      /**
       *  几何标记类型（9种）
       * .point() 点图
       * .line() 线图
       * .area() 面图
       * .path() 路径
       * .interval() 矩形或弧形
       * .polygon() 多边形
       * .edge() 两个点之间的链接
       * .heatmap() 热力图
       * .schema() 自定义
       *
       * .shape() 形状类型
       * .position(x*y) 坐标
       * .color() 点线面的颜色
       * .adjust('stack' || 'jitter' || 'dodge' || 'symmetric') 数据展示(层叠 || 扰动散开 || 分组散开 || 数据对称)
       * .label('value') 文本标签
       */
      chart
        // .line()
        // .shape('phone', ['dash', 'dot', 'smooth'])
        // .point()
        // .shape('phone', ['circle', 'square', 'triangle'])
        .area()
        .shape('phone', ['smooth'])
        .position('feature*value')
        .color('phone', ['#ff0000', '#00ff00', '#0000ff'])
        .adjust('stack')
        .size(5);

      /**
       *  坐标系配置
       * .coordinate('rect' || 'cartesian') 笛卡尔坐标系
       * .coordinate('helix') 螺旋坐标系
       * .coordinate('polar') 极坐标系
       * .coordinate('theta') 特殊极坐标系
       *
       * .transpose() // 坐标轴置换
       * .reflect('x' || 'y' || '') // 坐标轴轴镜像翻转
       */
      chart
        .coordinate('rect')
        .scale(1, 1)
        .rotate(0);

      /**
       *  度量配置
       * .scale({'': {}})
       * .scale('', {})
       */
      chart
        .scale('value', {
          min: 0,
          max: 1.5,
          type: 'linear',
          alias: '手机销售量',
          formatter: (val) => `${val}%`,
        });

      /**
       *  坐标轴配置
       * .axis('', {})
       */
      chart
        .axis('value', {
          title: {
            style: {
              fill: '#000000',
            },
          },
        });

      /**
       *  图例配置
       * .legend('', {})
       */
      chart.legend('value', {
        position: 'bottom',
      });

      /**
       *  提示信息配置
       * .tooltip({})
       */
      chart
        .tooltip({
          showCrosshairs: true, // 显示辅助线
          shared: true, // 合并数据项
          follow: true, // tooltip 跟随鼠标
        });

      chart.render();

      return chart;
    },

    /**
     * 配置项创建图表示例
     */
    createChart2(index) {
      const data = [
        { genre: 'Sports', sold: 275 },
        { genre: 'Strategy', sold: 115 },
        { genre: 'Action', sold: 120 },
        { genre: 'Shooter', sold: 350 },
        { genre: 'Other', sold: 150 },
      ];

      const chart = new Chart({
        container: `chart${index}`,
        width: 450,
        height: 300,
        options: {
          data,
          geometries: [
            {
              type: 'interval',
              position: 'genre*sold',
              color: 'genre',
            },
          ],
          interactions: [
            { type: 'active-region' },
          ],
          scales: {
            genre: { alias: '游戏种类' },
            sold: { alias: '游戏销售量', min: 0, max: 400 },
          },
          axes: {
            genre: { title: {} },
            sold: { title: {} },
          },
        },
      });

      chart.render();

      return chart;
    },

    /**
     *
     */
    createChart3(index) {
      const data = [
        { item: '事例一', count: 40, percent: 0.4 },
        { item: '事例二', count: 21, percent: 0.21 },
        { item: '事例三', count: 17, percent: 0.17 },
        { item: '事例四', count: 13, percent: 0.13 },
        { item: '事例五', count: 9, percent: 0.09 },
      ];

      const chart = new Chart({
        container: `chart${index}`,
        autoFit: true,
      });

      chart.data(data);

      chart
        .coordinate('theta', {
          radius: 0.75,
        });

      chart
        .scale('percent', {
          formatter: (val) => `${val * 100}%`,
        });

      chart
        .tooltip({
          showTitle: false,
          showMarkers: false,
        });

      chart
        .interval()
        .position('percent')
        .color('item')
        .adjust('stack')
        .label('percent', {
          content: (item) => `${item.item}: ${item.percent * 100}%`,
        });

      chart.interaction('element-active');

      chart.render();

      return chart;
    },

    createChart4(index) {
      const data = [
        { month: 'Apr', city: 'Tokyo', temperature: 14.5 },
        { month: 'Apr', city: 'London', temperature: 8.5 },
        { month: 'May', city: 'Tokyo', temperature: 18.4 },
        { month: 'May', city: 'London', temperature: 11.9 },
        { month: 'Jun', city: 'Tokyo', temperature: 21.5 },
        { month: 'Jun', city: 'London', temperature: 15.2 },
        { month: 'Jul', city: 'Tokyo', temperature: 25.2 },
        { month: 'Jul', city: 'London', temperature: 17 },
        { month: 'Aug', city: 'Tokyo', temperature: 26.5 },
        { month: 'Aug', city: 'London', temperature: 16.6 },
        { month: 'Sep', city: 'Tokyo', temperature: 23.3 },
        { month: 'Sep', city: 'London', temperature: 14.2 },
      ];

      const chart = new Chart({
        container: `chart${index}`,
        autoFit: true,
      });

      chart.data(data);

      chart
        .scale({
          month: {
            range: [0, 1],
          },
          temperature: {
            min: 0,
            max: 30,
            nice: true,
          },
        });

      chart
        .tooltip({
          showCrosshairs: true,
          shared: true,
        });

      chart
        .axis('temperature', {
          label: {
            formatter: (val) => `${val}°C`,
          },
        });

      chart
        .line()
        .position('month*temperature')
        .color('city')
        .shape('smooth');

      chart
        .point()
        .position('month*temperature')
        .color('city')
        .shape('circle');

      chart
        .coordinate('rect')
        .scale(0.8, 0.8)
        .rotate(0);

      chart.render();

      return chart;
    },
  },
};
</script>
<style lang="less" scoped>
