  min-height: calc(100% - 45px);
  padding: 20px;
  margin: 20px;
  background: #fff;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.btns {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  div {
    cursor: pointer;
    margin: 0 20px;
    width: 80px;
    height: 36px;
    line-height: 36px;
    border-radius: 5px;
    text-align: center;
    background: coral;
    color: #fff;
  }
}
.chart {
  background: ghostwhite;
  width: 500px;
  height: 350px;
  margin-bottom: 20px;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
