/**
 * 接口配置
 */

const MOCK_SWITCH = false;
const IS_PUBLIC = window.location.href.indexOf('public') !== -1;

const AOS_INTERFACE = {
  index: {
    test: 'https://api.apiopen.top/getJoke',
    public: 'https://api.apiopen.top/getJoke',
  },
  news: {
    test: 'https://api.apiopen.top/getJoke',
    public: 'https://api.apiopen.top/getJoke',
  },
};

const AOS_CONFIG = {};
Object.keys(AOS_INTERFACE).forEach((key) => {
  AOS_CONFIG[key] = IS_PUBLIC
    ? AOS_INTERFACE[key].public
    : AOS_INTERFACE[key].test;
});

module.exports = {
  MOCK_SWITCH,
  AOS_CONFIG,
};
