import { requestHandler, jsonpRequestHandler, mocksRequestHandler } from './service.control';
import { AOS_CONFIG, MOCK_SWITCH } from './service.config';

export default {
  /**
   * get 测试
   * @param {*} params
   */
  requestGet(params = {}) {
    console.log('params', params);
    const config = {
      url: AOS_CONFIG.news,
      methods: 'get',
      params: {
        type: 'all',
      },
    };

    if (MOCK_SWITCH) {
      return mocksRequestHandler('news');
    }

    return requestHandler(config);
  },

  /**
   * post 测试
   * @param {*} params
   */
  requestPost(params = {}) {
    console.log('params', params);
    const config = {
      url: AOS_CONFIG.news,
      methods: 'post',
      data: {
        type: 'all',
      },
    };

    if (MOCK_SWITCH) {
      return mocksRequestHandler('news');
    }

    return requestHandler(config);
  },

  /**
   * jsonp 测试
   * @param {*} params
   */
  requestJsonp(params = {}) {
    console.log('params', params);
    const config = {
      url: AOS_CONFIG.news,
      methods: 'jsonp',
      params: {
        type: 'all',
      },
    };

    if (MOCK_SWITCH) {
      return mocksRequestHandler('news');
    }

    return jsonpRequestHandler(config);
  },
};
